* {
    box-sizing: border-box;
}



html {
    line-height: 1.2;
}

body {
    margin: 0;
    padding: 0;
    font-size: 16px;
}

img {
    width: 100%;
}

h1,h2,h3,h4,h5,h6 {
    margin: 0 0;
}

a {
    text-decoration: none;
    color: inherit;
}


button {
    cursor: pointer;
}

